import axios from "axios";
import AuthHelper from './helper/AuthHelper';
const url = '/api/weather/';

class WeatherService{
    // Get location
    static async getLocation(searchName){
        if(searchName.length < 3) return;
        let params = {
            params: {
                k: searchName,
            }
        };
        let header = {...AuthHelper.getHeader(), ...params};
        return axios.get(url, header);
    }
    // Get location weather details
    static async getCurrentWeather(params){
        let getUrl = url + 'details'
        let header = {...AuthHelper.getHeader(), ...{params}};
        return axios.get(getUrl, header);
    }
}

export default WeatherService;