<template>
    <CRow>
        <CCol col="12" xl="12">
          <transition name="fade">
            <CCard>
              <CCardHeader>
                <CIcon name="cil-location-pin"/> 
                <span v-if="!showCityName">Search Location</span>
                <span v-else>Search Location - {{showCityName}}</span>
                <div class="card-header-actions">
                  <CLink
                  class="card-header-action btn-minimize"
                  @click="SearchformCollapsed=!SearchformCollapsed"
                  >
                    <CIcon :name="`cil-chevron-${SearchformCollapsed ? 'bottom' : 'top'}`"/>
                  </CLink>
                </div>
              </CCardHeader>
              <CCollapse :show="SearchformCollapsed">
                <CCardBody>
                    <CRow>
                      <CCol sm="4">
                          <CInput
                          horizontal
                          placeholder="City"
                          v-model="searchKeyword"
                          @change="searchLocation"
                          id="searchInput"
                          :readonly="isLoading"
                          />
                      </CCol>
                    </CRow>
                    <CRow alignHorizontal="end">
                      <CCol sm="12">
                        <CSpinner color="secondary" v-show="isLoading" />
                        <CRow v-show="isSearch">
                            <CCol v-for="result in SearchResult" :key="result.id" col="6" sm="3" md="2" class="p-1">
                              <CButton block color="primary" @click="showWeather(result.city_id, result.city_name, result.lat, result.lon)" :disabled="isLoading">[{{result.country_code}}] {{result.city_name}}</CButton>
                            </CCol>
                            <CCol v-show="Object.keys(SearchResult).length < 1" col="6" sm="3" md="2" class="p-1">
                              No record(s)
                            </CCol>
                        </CRow>
                      </CCol>
                    </CRow>
                </CCardBody>
              </CCollapse>
            </CCard>
          </transition>
          <CCard v-show="weatherResultShow">
            <CCardHeader>
              {{this.showCityName}} Weather
              <div class="card-header-actions">
                <CLink href="#" class="card-header-action btn-close" v-on:click="resetSearch">
                  <CIcon name="cil-x-circle"/>
                </CLink>
              </div>
            </CCardHeader>
            <CCardBody>
              <CRow v-if="Object.keys(WeatherResult.current).length > 0" alignHorizontal="end">
                <CCol col="12" sm="6" lg="4">
                  <CWidgetIcon
                    :text="this.WeatherResult.current.data[0].sunrise"
                    header="Sunrise"
                    color="gradient-info"
                  >
                    <fa :icon="['fas', 'sun']" />
                  </CWidgetIcon>
                  </CCol>
                  <CCol col="12" sm="6" lg="4">
                  <CWidgetIcon
                    :text="this.WeatherResult.current.data[0].sunset"
                    header="Sunset"
                    color="gradient-info"
                  >
                    <fa :icon="['fas', 'moon']" />
                  </CWidgetIcon>
                  </CCol>
                  <CCol col="12" sm="6" lg="4">
                  <CWidgetIcon
                    :text="(this.WeatherResult.current.data[0].app_temp !== this.WeatherResult.current.data[0].temp) ? `Feel like ${this.WeatherResult.current.data[0].app_temp}°` : ''"
                    :header="`Temperature ${this.WeatherResult.current.data[0].temp}°`"
                    color="gradient-info"
                  >
                    <fa :icon="['fas', 'temperature-low']" />
                  </CWidgetIcon>
                  </CCol>
              </CRow>
              <CRow v-if="Object.keys(WeatherResult.forecast).length > 0">
                <CCol v-for="(forcast, i) in WeatherResult.forecast.data" :key="forcast.id" sm="6" md="3" v-show="i> 0 && i < 8" >
                  <CCard class="text-center" body-wrapper>
                    <blockquote class="card-blockquote">
                      <p><strong>{{forcast.datetime}}</strong></p>
                      <img :src="`https://www.weatherbit.io/static/img/icons/${forcast.weather.icon}.png`">
                      <p>{{forcast.weather.description}}</p>
                      <p><fa :icon="['fas', 'caret-down']" class="text-success" /> {{forcast.low_temp}}° <fa :icon="['fas', 'caret-up']" class="text-danger" /> {{forcast.high_temp}}°</p>
                    </blockquote>
                  </CCard>
                </CCol>
              </CRow>
            </CCardBody>
          </CCard>
        </CCol>
    </CRow>
</template>

<script>
import WeatherService from '../../services/WeatherService'
export default {
  name: 'Weather',
  data () {
    return {
      SearchformCollapsed: true,
      loading: false,
      searchKeyword: '',
      showCityName: '',
      isSearch: false,
      SearchResult: {},
      isLoading: false,
      WeatherResult: {
        current: {},
        forecast: {}
      },
      weatherResultShow: false
    }
  },
  created() {
  },
  methods: {
    async searchLocation(){
      this.SearchResult = {};
      try{
        if(this.searchKeyword.length < 3){
          return;
        }
        this.isLoading = true;
        let WeatherDatas = WeatherService.getLocation(this.searchKeyword);
        WeatherDatas.then( (res) =>{
          this.isSearch = true
          this.SearchResult = res.data
          this.showCityName = ''
          this.WeatherResult = {
            current: {},
            forecast: {}
          };
          this.weatherResultShow = false
          this.isLoading = false;
        })
        .catch((err) => {
          this.isLoading = false;
        });
      }catch(error) {
        console.log(error);
      };
      // this.isLoading = false;
    },
    async showWeather(city_id, city_name, lat, lon){
      this.isLoading = true;
      let params = {lat: lat, lon: lon}
      let WeatherDatas = WeatherService.getCurrentWeather(params);
      WeatherDatas.then( (res) =>{
        this.isSearch = false
        this.SearchResult = {}
        this.weatherResultShow = true;
        this.showCityName = city_name
        this.SearchformCollapsed = false
        this.WeatherResult = res.data
        this.isLoading = false;
      } )
    },
    resetSearch(){
      this.SearchformCollapsed = true
      this.weatherResultShow = false
      this.isSearch = false
      this.searchKeyword = ''
      this.showCityName = ''
    }
  }
}
</script>
